<template>
  <div>
    <v-card max-width="445">
      <v-img
          src="https://picsum.photos/id/1062/300/300"
      ></v-img>
      <v-card-title>Manta para mascotas</v-card-title>
      <v-card-subtitle>PRECIO: 14.99 PEN</v-card-subtitle>
      <v-card-text>
        Increíble manta para engreir a tu mascota con tacto de seda fino y de gran calidad
      </v-card-text>
      <v-card-actions>
        <div v-if="payNow">
          <script type="application/javascript"
                  src="https://www.mercadopago.com.pe/integrations/v1/web-payment-checkout.js"
                  :data-preference-id=preferenceId>
          </script>
        </div>
        <div v-else>
          <v-btn @click="goToCheckout" :loading="btnLoading" color="primary">
            Pasar por caja
          </v-btn>
        </div>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
import http from '@/services/http-common'

export default {
  name: 'Home',
  data() {
    return {
      id: this.$route.params.id,
      payNow: false,
      preferenceId: "0",
      btnLoading: false,
    }
  },
  methods: {
    goToCheckout(){
      this.btnLoading = true;
      http.post('/payments').then(response =>{
        console.log(response);
        this.preferenceId = response.id;
        this.payNow = true;
        this.btnLoading = false;
      }).catch(e => console.log(e))
    }
  }
}
</script>
